.entry {  
    &::after {
        content: '';
        display: table;
        clear: both;
    }

    &.post {
        margin-bottom: 25px;
    }

    img {
        max-width: 100%;
    }

    &-title {
        -ms-word-wrap: break-word;
        word-wrap: break-word;
        padding-top: 1rem;

        a {
            color: $gray-900;
        }
    }
    
    /* Meta */
    &-meta {
        margin-bottom: .5rem;
    
        svg {
            width: 15px;
            height: 15px;
            vertical-align: -8%;
            fill: #949494;
        }
    }
    
    /* Media Image */
    &-media {
        position: relative;
        float: left;
        margin: 0 15px 15px 0;
        overflow: hidden;
        border: 1px solid $gray-200;
        border-radius: 3px;
        width: 100%;
        max-width: 200px;

        @include media-breakpoint-down(sm) {
            max-width: 100%;
            margin-right: 0;
        }
    
        &__link {
            display: block;
        }
    
        .ls-blur-up-img,
        &__src {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate3d(-50%,-50%,0);
            /* only if you want to change the blur-up option from always to auto or want to use blur up effect without a lowsrc image. */
            font-family: "blur-up: auto", "object-fit: cover";
            object-fit: cover;
    
            &.match {
                width: 100%; 
                height: 100%;
            }
    
            &.taller {
                width: 100%; 
                height: auto;
            }
    
            &.wider {
                width: auto;
                height: 100%;
            }

            &.ls-blur-up-is-loading,
            &.lazyload:not([src]) {
                visibility: hidden;
            }
        }

        .ls-blur-up-img {
            filter: blur(10px);
            opacity: 1;
            transition: opacity 1000ms, filter 1500ms;
        }
    
        .ls-blur-up-img.ls-inview.ls-original-loaded {
            opacity: 0;
            filter: blur(5px);
        }
    } 
}   

.hfeed {
    .entry {
        &-footer {
            text-align: right;
        }
    }
}